:host ::ng-deep {
  .p-menu {
    min-width: 3.5rem;
    background: var(--navigation-background-color);
    border: none;
  }

  .p-menu-item-link {
    color: var(--navigation-font-color);
    font-size: var(--navigation-font-size);
    font-weight: var(--navigation-font-weight);
    text-transform: uppercase;
    position: relative;
    transition: all 0.2s ease;

    // Hover state - highlight entire row
    &:hover {
      background-color: rgba(
        var(--color-primary-rgb, 66, 133, 244),
        0.1
      ) !important;

      // Ensure text color is visible on hover
      .small-caps-text {
        color: var(--navigation-font-color);
      }
    }
  }

  .p-menuitem-link-active {
    border-radius: 0 !important;
    color: var(--color-primary-color) !important;
    background: rgba(var(--color-primary-rgb, 66, 133, 244), 0.08) !important;

    // Active state - highlight entire text
    .small-caps-text {
      color: var(--color-primary-color) !important;
    }

    &::before {
      content: '';
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      width: 3px;
      background-color: var(--color-primary-color);
    }
  }

  .p-menu-list {
    padding: 0;
  }

  .p-menu-item:not(.p-disabled) .p-menu-item-content:hover {
    opacity: 1;
    background: inherit;
  }

  .p-menu-item.p-focus .p-menu-item-content {
    background: inherit;
  }

  .w-aside {
    width: var(--width-aside);
  }
}

.sidebar-container {
  border-right: solid var(--navigation-separator-width)
    var(--navigation-separator-color);
  border-color: var(--navigation-border-color);
  border-width: var(--navigation-border-width);
  background: var(--navigation-background-color);
  transition: width 0.3s;

  @media (max-width: 767px) {
    display: none; // Hide sidebar on smaller screens
    z-index: 70; // Ensure the sidebar is above other content
    &.open {
      display: flex;
      position: fixed;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
    }
  }
}
